.thumbnail-container {
    .column {
        img {
           height: 110px; 
           vertical-align: middle;

        }
    }

    
  .next-slide {
    padding: 25px;
  }
}

.mySlides 
{
    img {
    height: 680px;
    width: 100%;
  }
}
  
  /* Position the image container (needed to position the left and right arrows) */
  .thumbnail-container {
    position: relative;
  }
  
  /* Hide the images by default */
  .mySlides {
    display: none;
  }
  
  /* Add a pointer when hovering over the thumbnail images */
  .cursor {
    cursor: pointer;
  }
  
  /* Container for image text */
  .caption-container {
    text-align: center;
    background-color: #222;
    padding: 2px 16px;
    color: white;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
   /* Add a transparency effect for thumnbail images */
  .demo {
    opacity: 0.6;
  }
  
  .active,
  .demo:hover {
    opacity: 1;
  }