
/* Mobile View */
@media screen and (min-device-width: 320px) and (max-device-width: 767px){
    .logo-btm {display: none;}
    .btm-footer {display: flex;flex-direction: column;border-top: 1px solid #ddd;margin-top: 0px !important;}
    .btmtxt {margin-top: 14px;}
    .footer {padding: 0px !important;}
    .container {padding: 10px !important;}
    .footer-col {width:100% !important; margin-bottom: 25px;}
    .footer-col h4 {margin-bottom: 10px;}
    .foo-container{padding: 0px !important;}

    .btm-footer{
        display: flex;
        flex-direction: column-reverse;
    }
    .content {
        margin: 25px 0 38px;
    }
    
}



.foo-container{
    margin: auto;
    text-align: left;
    padding: 0px 135px 0px 135px;
}

ul{
    list-style: none;
}
.row{
    display: flex;
    flex-wrap: wrap;
}

footer{
    background-color: #ffffff;
    padding: 50px 0;
    border-top: 1px solid #ddd;
}

.footer-col{
    width: 25%;
    padding: 0 15px;
}

.footer-col h4 {
    font-size: 16px;
    color: #333;
    text-transform: capitalize;
    margin-bottom: 20px;
    font-weight: 600;
    position: relative;
}

.footer-col h4::befor{
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    background-color: #333;
    height: 2px;
    box-sizing: border-box;
    width: 50px;
}
.footer-col p{
    font-size: 16px;
    line-height: 22px;
}

.footer-col ul li:not(:last-child){
    margin-bottom: 8px;
}

.footer-col ul li a{
    font-size: 16px;
    text-transform: capitalize;
    color: #333;
    text-decoration: none;
    font-weight: 300;
    color:black;
    display: block;
}

.btm-footer{
	display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 10%;
    background-color: #ffffff;
    border: 1px solid #ddd;
    font-family: "Mulish", sans-serif;
    color: #1b252c;
    font-size: 16px;
    margin-top: 40px;
    }
    
    .logo-btm{
    cursor: pointer;
    width: 10%;
    }

    .icons{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        margin-top: 10px; 
    }

    .btmtxt{
        display: flex;
    }
    
    .btm-footer > ul > li > a {
        color: #1b252c;
    }

    .list{
        margin-left: 10px;
        text-decoration: underline;
    }
    .insta, .fcb, .twt{
    height: 20px;
    }