.shopping-cart-section {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr 2fr;
    gap: 1em;
}

.cart-img-section {
    border: 1px solid #ddd;
    padding: 5px;
    height: 130px;
}

.cartimg {
    height: 100%;
    width: 100%;
    object-fit: contain;
    object-position: 50% 50%;
}

.handdle-section {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 16px;
}

.edit-img,
.save-img,
.remove-img {
    margin-right: 10px;
}

.quantity-cart {
    display: flex;
    align-items: self-start;
}

.minuscount-img-cart,
.pluscount-img-cart {
    margin-top: 10px;
}

.cart-content-title {
    /* UI Properties */
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    text-align: left;
    font: normal normal 600 20px/25px Muli;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
}